
#_pi_surveyWidgetContainer {
	margin:25px;
	border:2px solid #000;
	border-radius: 5px;
}

._pi_closeButton {
	display: none;
}

._pi_question {
	text-align: center !important;
	font-size: 0.9em;
	margin: 0;
	background-color: navy;
	padding: 10px 0;
}

._pi_question_multiple_choices_question {
	background-color: green;
}


ul._pi_answers_container li a label,
ul._pi_answers_container li label._pi-control {
	font-size: 0.8em;
}

a._pi_branding {
	display:block;
	position:relative;
	margin:0;
	padding:10px;
	bottom:0;
	top:0;
	left:0;
	background-color:#eeeeee;
	color: #000;
/* 	display: none !important; */
}

/* ------------------------------ PLACEHOLDER VISUAL STYLES (COLORS, ETC.) -------------- */
ul._pi_answers_container {
	background-color: lightblue;
	min-width: 320px !important;
}

	ul._pi_answers_container li,
	ul._pi_answers_container li a,
	ul._pi_answers_container li a label,
	ul._pi_answers_container li label._pi-control-checkbox {
		background-color: lightblue;
		color: black;
		text-decoration: none;
		text-align: left;
	}

	ul._pi_answers_container li:hover,
	ul._pi_answers_container li:hover a,
	ul._pi_answers_container li:hover a label,
	ul._pi_answers_container li:hover label._pi-control-checkbox {
		background-color: brown;
		color: white;
		text-decoration: underline;
	}

	ul._pi_answers_container li {
/* 		flex: 0 0 31%; */
	}



		/* SINGLE CHOICE ONLY ------------------------------- */
		ul._pi_answers_container li a {			
			background-color: lightgray;
		}

			ul._pi_answers_container li a span._pi_radio_button_outer {
				background-color: rgba(255,255,255,0.2);
				border: 1px solid rgba(255, 255, 255, 0.5);
			}

				ul._pi_answers_container li:hover a span._pi_radio_button_inner {
					background-color: rgba(255, 255, 255, 0.5);
					border: 1px solid transparent;
				}

			ul._pi_answers_container li a label {
				background-color: ivory;
			}



		/* MULTIPLE CHOICE ONLY ------------------------------- */
		ul._pi_answers_container li label._pi-control-checkbox {		
			background-color: lightgreen;
		}

			._pi-control-indicator {
				background-color: rgba(255,255,255,0.2);
			}

		/* Maximum Count - Container */
		/* Submit Button - Container */
		div[data-question-type="multiple_choices_question"] ._pi_free_text_question_field_container,
		div[data-question-type="multiple_choices_question"] ._pi_multiple_choices_question_submit_button_container {
			background-color: rgba(255,255,255,0.3);
		}